.homepage {
    display: flex;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    background-image: url('https://images.unsplash.com/photo-1592677707622-1b7ade22696e?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&dl=ghenadie-cebanu-CYoRRCbl8Ws-unsplash.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Open Sans', sans-serif;

    .sign-up {
        background-color: rgba($color: #fff, $alpha: 0.5);
        display: flex;
        justify-content: center;
        flex-direction: column;
        width: 580px;
        height: 100%;
        padding: 50px;
      
        .title {
          margin: 10px 0;
        }
      }

      .sign-in-block{
        display: block;
        margin-top: 20px;
      }
  }

